// ** React Imports
import { Link } from 'react-router-dom'
import { useRef, useState, Fragment } from 'react'
import themeConfig from '@src/configs/themeConfig'
// ** Custom Components
import Wizard from '@components/wizard'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
// ** Reactstrap Imports
import { Row, Col, CardBody, Card } from 'reactstrap'

// ** Third Party Components
import { Home, User, CheckCircle } from 'react-feather'

// ** Steps
import Verify from './steps/Verify'
import Organization from './steps/Organization'
import AccountDetails from './steps/AccountDetails'

// ** Styles
import '@styles/react/pages/page-authentication.scss'

const RegisterMultiSteps = () => {
  // ** Ref
  const ref = useRef(null)

  // ** State
  const [stepper, setStepper] = useState(null)
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  const loaderCallBack = (val) => {

    setBlock(val)
  }

  const steps = [

    {
      id: 'account-details',
      title: 'Account',
      subtitle: 'Enter User Details',
      icon: <User size={18} />,
      content: <AccountDetails stepper={stepper} loaderCallBack={loaderCallBack} />
    },
    {
      title: 'Verify',
      id: 'account-verification',
      subtitle: 'Account Verification',
      icon: <CheckCircle size={18} />,
      content: <Verify stepper={stepper} loaderCallBack={loaderCallBack} />
    },
    {
      id: 'create-organization',
      title: 'Organization',
      subtitle: 'Create Organization',
      icon: <Home size={18} />,
      content: <Organization stepper={stepper} loaderCallBack={loaderCallBack} />
    }
  ]

  const source = require('@src/assets/images/pages/create-account.svg').default

  return (
    <div className='auth-wrapper auth-cover'>
      <UILoader blocking={block} loader={<Loader />}>
        <Row className='auth-inner m-0'>
          <Link className='brand-logo' to='/'>
            <img src={themeConfig.app.appLogoImage} alt={'Turia Logo'} width={45} height={35} />
            <h1 className='brand-text text-primary'>Turia</h1>
          </Link>
          <Col lg='3' className='d-none d-lg-flex align-items-center p-0'>
            <div className='w-100 d-lg-flex align-items-center justify-content-center'>
              <img className='img-fluid w-100' src={source} alt='Login Cover' />
            </div>
          </Col>
          <Col lg='9' className='d-flex align-items-center px-2 px-sm-3 px-lg-5 pt-3'>
            <Card className='mx-auto'>
              <CardBody>
                <Wizard
                  ref={ref}
                  steps={steps}
                  instance={el => setStepper(el)}
                  headerClassName='px-0'
                  contentWrapperClassName='px-0 mt-1'
                  className='register-multi-steps-wizard shadow-none'
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </UILoader>
    </div>
  )
}

export default RegisterMultiSteps
