// ** React Imports
import { Fragment, useEffect, useContext, useState } from 'react'
import Avatar from '@components/avatar'
import * as yup from 'yup'
import { useForm, Controller } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import Select from 'react-select'
import { ChevronLeft, Check, Coffee, X } from 'react-feather'
import classnames from 'classnames'
import axios from '@src/configs/axios/axiosConfig'
import { getHomeRouteForLoggedInUser } from '@utils'
import { createOrganization, createOrganizationUser, createOrganizationPlanPackage } from '../store/index'
import toast from 'react-hot-toast'
import { Label, Input, Row, Col, Button, FormFeedback } from 'reactstrap'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import moment from 'moment'
import { AbilityContext } from '@src/utility/context/Can'

const ToastContent = ({ t, name }) => {
  return (
    <div className='d-flex'>
      <div className='me-1'>
        <Avatar size='sm' color='success' icon={<Coffee size={12} />} />
      </div>
      <div className='d-flex flex-column'>
        <div className='d-flex justify-content-between'>
          <h6>{name}</h6>
          <X size={12} className='cursor-pointer' onClick={() => toast.dismiss(t.id)} />
        </div>
        <span>You have successfully logged in as an user to Turia. Now you can start to explore. Enjoy!</span>
      </div>
    </div>
  )
}

const Organization = ({ stepper, loaderCallBack }) => {
  const ability = useContext(AbilityContext)
  const dispatch = useDispatch()
  const navigate = useNavigate({})
  const [isGstRegistered, setisGstRegistered] = useState(true)
  const [countryCode, setCountryCode] = useState(null)
  const [businessEntityIdOptions, setBusinessEntityIdOptions] = useState([])
  const [businessTypesOptions, setBusinessTypesOptions] = useState([])
  const [currencyOptions, setCurrencyOptions] = useState([])
  const [stateOptions, setStateOptions] = useState([])
  const [countryOptions, setCountryOptions] = useState([])
  const store = useSelector(state => state.register)
  const phoneRegExp = /^[0-9\- ]{10,10}$/
  const pinzipcodeRegExp = /^[0-9\- ]{6,6}$/
  const gstinRegExp = /^[0-9]{2}[A-Z]{5}[0-9]{4}[A-Z]{1}[1-9A-Z]{1}Z[0-9A-Z]{1}$/

  const OrganizationSchema = yup.object().shape({

    name: yup.string().required('Please Enter Business Name'),
    businessEmail: yup.string().email().required('Please Enter Business Email'),
    contactNo: yup.string().required('Please Enter Contact Number').matches(phoneRegExp, { message: 'Phone number is not valid', excludeEmptyString: true }),
    countryCode: yup.string(),
    currencyId: yup.string().required('Please Select Currency'),
    businessEntityId: yup.string().when("countryCode", { is: (countryCode) => countryCode === 'IN', then: yup.string().required('Please Select Business Entity') }),
    businessTypeId: yup.string().when("countryCode", { is: (countryCode) => countryCode === 'IN', then: yup.string().required('Please Select Business Type') }),
    isGstRegistered: yup.boolean().default(true),
    gstin: yup.string().when("isGstRegistered", { is: (isGstRegistered) => isGstRegistered === true, then: yup.string().required("GSTIN Required.").matches(gstinRegExp, { message: 'GSTIN is not valid', excludeEmptyString: true }) }),
    addressLine1: yup.string().required('Please Enter Address Line 1'),
    addressLine2: yup.string().default('').when("countryCode", { is: (countryCode) => countryCode === 'IN', then: yup.string().required('Please Enter Area, Street, Sector, Village') }),
    countryId: yup.string().required('Please Select CountryId'),
    stateId: yup.string().when("countryCode", { is: (countryCode) => countryCode === 'IN', then: yup.string().required('Please Select State') }),
    stateOther: yup.string().default('').nullable(),
    pinZipCode: yup.string().default('').when("countryCode", { is: (countryCode) => countryCode === 'IN', then: yup.string().required('Please Enter Postal Code').matches(pinzipcodeRegExp, { message: 'Postal Code is not valid', excludeEmptyString: true }) }),
    city: yup.string().default('').when("countryCode", { is: (countryCode) => countryCode === 'IN', then: yup.string().required('Please Enter City') })
  })

  const {
    control,
    handleSubmit,
    formState: { errors }, setError, setValue
  } = useForm({
    defaultValues: OrganizationSchema.cast(),
    resolver: yupResolver(OrganizationSchema)
  })

  const onSubmit = async data => {
    data.userId = store.loginUser.id
    await dispatch(createOrganization(data))
  }

  const getBusineessEntity = () => {
    axios.post('/businessentities/list').then(response => {
      const arr = response.data
      setBusinessEntityIdOptions(arr.businessentities)
    })
  }

  const getCurrencies = () => {
    axios.post('/currencies/dropdown').then(response => {
      const arr = response.data
      setCurrencyOptions(arr.currencies)
    })
  }

  const getBusineessTypes = () => {
    axios.post('/businesstypes/dropdown').then(response => {
      const arr = response.data
      setBusinessTypesOptions(arr.businesstypes)
    })
  }

  const getCountries = () => {
    axios.post('/countries/dropdown').then(response => {
      const arr = response.data
      setCountryOptions(arr.countries)
    })
  }

  const getStates = () => {
    axios.post('/states/list').then(response => {
      const arr = response.data
      setStateOptions(arr.states)
    })
  }

  const setAcl = async (ogid, userid) => {
    const res = await axios.post('/organizationusers/acl', { organizationId: ogid, userId: userid })

    const abilitytemp = []
    res.data.ability.forEach(obj => {
      const action = obj.substring(obj.lastIndexOf("-") + 1)
      const resource = obj.replace(obj.substring(obj.lastIndexOf("-")), '')
      const opt = { action, subject: resource }
      abilitytemp.push(opt)
    })
    localStorage.setItem('ability', JSON.stringify(abilitytemp))
    await ability.update(abilitytemp)
    await navigate(getHomeRouteForLoggedInUser('admin'))
  }

  const createOrgUser = async (user, orgid) => {
    const data = {
      userId: user.id,
      name: user.name,
      organizationId: orgid,
      email: user.email,
      userTypeId: user.accounttype
    }

    const planpackage = {
      organizationId: orgid,
      startdate: moment().valueOf(),
      enddate: moment().add(15, 'days').valueOf()
    }
    loaderCallBack(true)
    await dispatch(createOrganizationUser(data))
    await dispatch(createOrganizationPlanPackage(planpackage))
    await setAcl(orgid, user.id)
    loaderCallBack(false)
  }

  useEffect(async () => {
    if (!store.verifyprocess) {
      getBusineessEntity()
      getBusineessTypes()
      getCountries()
      getCurrencies()
      getStates()
    }
  }, [store.verifyprocess])

  useEffect(async () => {
    if (store.registerSuccess) {
      loaderCallBack(true)
      const activeOrg = store.activeOrganization      
      localStorage.setItem('activeOrganization', JSON.stringify(activeOrg))
    }
  }, [store.registerSuccess])

  useEffect(() => {

    if (store.activeOrganizationId !== null) {
      const user = store.loginUser
      const orgid = store.activeOrganizationId
      createOrgUser(user, orgid)
      toast(t => (
        <ToastContent t={t} name={user.name} />
      ))
    }

  }, [store.activeOrganizationId])

  useEffect(async () => {

    if (store.organizationCreateError !== null) {
      Object.keys(store.organizationCreateError).map((obj) => {
        setError(obj, { type: 'custom', message: store.organizationCreateError[obj][0] })
      })
    }
  }, [store.organizationCreateError])


  const getRow = (fieldLabel, fieldName, reqflag = false) => {
    return (
      <Row className=''>
        <Label sm='12' size='lg' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName}>
          {fieldLabel}
        </Label>
        <Col sm='12'>
          <Controller
            id={fieldName}
            name={fieldName}
            control={control}
            render={({ field }) => <Input invalid={errors[fieldName] && true} {...field} />}
          />
          {errors[fieldName] && <FormFeedback>{errors[fieldName].message}</FormFeedback>}
        </Col>
      </Row>
    )
  }

  const getSelectRow = (fieldLabel, fieldName, options, reqflag = false) => {
    return (

      <Row className=''>
        <Label sm='12' size='lg' className={classnames(`form-label ${reqflag ? 'required' : ''}`)} for={fieldName} >
          {fieldLabel}
        </Label>
        <Col sm='12'>
          <Controller
            control={control}
            name={fieldName}
            id={fieldName}
            render={({ field, ref }) => (
              <Select
                inputRef={ref}
                className={classnames('react-select', { 'is-invalid': errors[fieldName] })}
                {...field}
                classNamePrefix='select'
                options={options}
                value={options.find(c => { return c.id === field.value })}
                onChange={val => {
                  if (fieldName === 'countryId') {
                    setCountryCode(val.shortname)
                    setValue('CountryCode', val.shortname)
                    if (val.shortname === 'IN') {
                      setValue('isGstRegistered', true)
                      setisGstRegistered(true)
                    } else {
                      setValue('isGstRegistered', false)
                      setisGstRegistered(false)
                    }
                  }
                  return field.onChange(val.id)
                }}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
              />
            )}

          />
          {errors[fieldName] && <FormFeedback className='text-danger'>{errors[fieldName]?.message}</FormFeedback>}
        </Col>
      </Row>

    )
  }


  return (
    <Fragment>
      <div className='content-header mb-75'>
        <h2 className='fw-bolder mb-75'>Organization Information</h2>
        <span>Enter Your Organization Details.</span>
      </div>
      <form onSubmit={handleSubmit(onSubmit)}>

        {Object.keys(errors).map((obj, k) => {
          return <FormFeedback key={k}> {errors[obj]?.message}</FormFeedback>
        })}

        {errors['action'] && <p className='text-danger'>{errors['action']?.message}</p>}
        <Row>
          <Col md='6' >
            {getRow('Business Name', 'name', true)}
          </Col>
          <Col md='6' >
            {getRow('Business E-mail', 'businessEmail', true)}
          </Col>
          <Col md='6' >
            {getRow('Conatct No', 'contactNo', true)}
          </Col>
          <Col md='6'>
            {getSelectRow('Country', 'countryId', countryOptions, true)}
          </Col>
          <Col md='6'>
            {getSelectRow('Currency', 'currencyId', currencyOptions, true)}
          </Col>
          <Col md='6'></Col>

          {(countryCode === null || countryCode === 'IN') &&
            <Fragment>
              <Col md='6'>
                {getSelectRow('Business Type', 'businessTypeId', businessTypesOptions, true)}
              </Col>
              <Col md='6'>
                {getSelectRow('Business Entity', 'businessEntityId', businessEntityIdOptions, true)}
              </Col>
              <Col md='6' className='mt-1'>
                <Label className='form-label' for='pincode'>
                  Is GST Registered?
                </Label>
                <div className='form-switch form-check-success'>
                  <Input type='switch' id='switch-success' name='isGstRegistered' defaultChecked onChange={(e) => { setisGstRegistered(e.target.checked); setValue('isGstRegistered', e.target.checked) }} />
                </div>
              </Col>
              <Col md='6'>
                {isGstRegistered ? getRow('GSTIN', 'gstin', true) : getRow('GSTIN', 'gstin', false)}
              </Col>
            </Fragment >
          }
          <Col sm='12' >
            {getRow('Address', 'addressLine1', true)}
          </Col>
          <Col sm={12}>
            {(countryCode === null || countryCode === 'IN') ? getRow('Area, Street, Sector, Village', 'addressLine2', true) : getRow('Area, Street, Sector, Village', 'addressLine2', false)}
          </Col>
          <Col md='6'>
            {(countryCode === null || countryCode === 'IN') ? getSelectRow('State', 'stateId', stateOptions, true) : getRow('State', 'stateOther', false)}
          </Col>
          <Col md='6'>
            {(countryCode === null || countryCode === 'IN') ? getRow('Postal Code', 'pinZipCode', true) : getRow('Postal Code', 'pinZipCode', false)}
          </Col>
          <Col md='6'>
            {(countryCode === null || countryCode === 'IN') ? getRow('Town/City', 'city', true) : getRow('Town/City', 'city', false)}
          </Col>
        </Row>
        <div className='d-flex justify-content-between mt-2'>
          <Button color='secondary' className='btn-prev' outline onClick={() => stepper.previous()}>
            <ChevronLeft size={14} className='align-middle me-sm-25 me-0'></ChevronLeft>
            <span className='align-middle d-sm-inline-block d-none'>Previous</span>
          </Button>
          <Button type='submit' color='success' className='btn-next'>
            <span className='align-middle d-sm-inline-block d-none'>Submit</span>
            <Check size={14} className='align-middle ms-sm-25 ms-0'></Check>
          </Button>
        </div>
      </form>
    </Fragment>
  )
}

export default Organization
