// ** React Imports
import { Fragment, useEffect } from 'react'

// ** Third Party Components
import * as yup from 'yup'
import { useForm, Controller } from 'react-hook-form'
import { yupResolver } from '@hookform/resolvers/yup'
import { ChevronLeft, ChevronRight } from 'react-feather'

import { register, generateCode } from '../store/index'
import { useDispatch, useSelector } from 'react-redux'
// ** Reactstrap Imports
import { Form, Label, Input, Row, Col, Button, FormFeedback } from 'reactstrap'

// ** Custom Components
import InputPasswordToggle from '@components/input-password-toggle'
import { Link } from 'react-router-dom'

const defaultValues = {
  email: '',
  name: '',
  password: '',
  confirmPassword: ''
}

const AccountDetails = ({ stepper, loaderCallBack }) => {

  const dispatch = useDispatch()
  const passwordRegx = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/

  const store = useSelector(state => state.register)
  const SignupSchema = yup.object().shape({
    firstname: yup.string().default('').required('FirstName is Reuired'),
    lasttname: yup.string().default(''),
    email: yup.string().email().required('Email is Required'),
    password: yup.string().required('Password').matches(
      passwordRegx,
      "Password must Contain 8 Characters, One Uppercase, One Lowercase, One Number and one special case Character"),
    confirmPassword: yup
      .string()
      .required('Confirm Password is Required')
      .oneOf([yup.ref(`password`), null], 'Passwords must match')
  })

  // ** Hooks
  const {
    control,
    handleSubmit,
    formState: { errors }, setError
  } = useForm({
    defaultValues,
    resolver: yupResolver(SignupSchema)
  })

  const generateVerifyCode = async (useremail) => {
    if (useremail !== null) {
      const obj = { email: useremail }
      await dispatch(generateCode(obj))
      stepper.next()
    }
  }

  const onSubmit = async data => {
    loaderCallBack(true)
    data['name'] = `${data.firstname} ${data.lastname ?? ''}`
    await dispatch(register(data))
    loaderCallBack(false)
  }

  useEffect(async () => {
    if (store.loginUser !== null) {
      if (store.loginUser.status === 17) {
        stepper.next()
        stepper.next()
      } else {
        generateVerifyCode(store.loginUser.email)
      }
    }
    if (store.loginError !== null) {
      Object.keys(store.loginError).map((obj) => {
        setError(obj, { type: 'custom', message: store.loginError[obj][0] })
      })
    }
  }, [dispatch, store.loginUser, store.loginError])

  return (
    <Fragment>
      <div className='content-header mb-2'>
        <h2 className='fw-bolder mb-75'>Account Information</h2>
        <span>Enter your name password details</span>
      </div>
      <Form onSubmit={handleSubmit(onSubmit)}>
        <Row>
          <Col md='6' className='mb-1'>
            <Label className='form-label required' for='name'>
              Firstname
            </Label>
            <Controller
              id='firstname'
              name='firstname'
              control={control}
              render={({ field }) => <Input placeholder='johndoe' invalid={errors.firstname && true} {...field} />}
            />
            {errors.firstname && <FormFeedback>{errors.firstname.message}</FormFeedback>}
          </Col>
          <Col md='6' className='mb-1'>
            <Label className='form-label' for='name'>
              Lastname
            </Label>
            <Controller
              id='lastname'
              name='lastname'
              control={control}
              render={({ field }) => <Input placeholder='johndoe' invalid={errors.lastname && true} {...field} />}
            />
            {errors.lastname && <FormFeedback>{errors.lastname.message}</FormFeedback>}
          </Col>
          <Col md='6' className='mb-1'>
            <Label className='form-label required' for={`email`}>
              Email
            </Label>
            <Controller
              control={control}
              id='email'
              name='email'
              render={({ field }) => (
                <Input type='email' placeholder='john.doe@email.com' invalid={errors.email && true} {...field} />
              )}
            />
            {errors.email && <FormFeedback>{errors.email.message}</FormFeedback>}
          </Col>
        </Row>
        <Row>
          <Col md='6' className='mb-1'>
            <Label className='form-label required' for={`password`}>
              Password
            </Label>
            <div className='form-password-toggle col-md-12 mb-1'>
              <Controller
                id='password'
                name='password'
                control={control}
                render={({ field }) => (
                  <InputPasswordToggle
                    htmlFor='password'
                    className='input-group-merge'
                    invalid={errors.password && true}
                    {...field}
                  />
                )}
              />
              {errors.password && <FormFeedback>{errors.password.message}</FormFeedback>}
            </div>
          </Col>
          <Col md='6' className='mb-1'>
            <Label className='form-label required' for={`password`}>
              Confirm Password
            </Label>
            <div className='form-password-toggle col-md-12 mb-1'>
              <Controller
                control={control}
                id='confirmPassword'
                name='confirmPassword'
                render={({ field }) => (
                  <InputPasswordToggle
                    htmlFor='confirmPassword'
                    className='input-group-merge'
                    invalid={errors.confirmPassword && true}
                    {...field}
                  />
                )}
              />
              {errors.confirmPassword && <FormFeedback>{errors.confirmPassword.message}</FormFeedback>}
            </div>
          </Col>
        </Row>
        <Row>
          <Col sm={12} className='mb-1'>
            <div className='form-check form-check-inline'>
              <Input type='checkbox' id='remember-me' />
              <Label for='remember-me' className='form-check-label'>
                Remember Me
              </Label>
            </div>
          </Col>
        </Row>
        <div className='d-flex justify-content-between mt-2'>
          <Button color='secondary' className='btn-prev' outline disabled>
            <ChevronLeft size={14} className='align-middle me-sm-25 me-0'></ChevronLeft>
            <span className='align-middle d-sm-inline-block d-none'>Previous</span>
          </Button>
          <Button type='submit' color='primary' className='btn-next'>
            <span className='align-middle d-sm-inline-block d-none'>Next</span>
            <ChevronRight size={14} className='align-middle ms-sm-25 ms-0'></ChevronRight>
          </Button>
        </div>
      </Form>
      <p className='text-center mt-2'>
        <span className='me-25'>Already Have an Account?</span>
        <Link to='/login'>
          <span>Login</span>
        </Link>
      </p>
    </Fragment>
  )
}

export default AccountDetails
